<template>
  <div class="main">
    <div class="h1">背景带有颜色，并且能够获取到数据</div>
    <div class="green" v-if="!show">未获取到数据</div>
    <div class="red" v-else>
      <img class="title-pic" :src="res.data.data.bg_pic">
      <div>{{res.data.data}}</div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';

export default {
  data() {
    return {
      show: false,
      res: {}
    }
  },
  created: function () {
    this.$axios.get('http://test1-api.dongqiudi.com/v2/activity/hisensetv/3260453').then(
      res => {
        console.log(11111111)
        this.show = true
        this.res = res
      }
    )
  },
  mounted: function () {

  },
  updated: (a) => {

  },
  computed: {
  },
  methods: {

  }
}
</script>

<style lang="less">
@import "./index.less";
@px2rem: 37.5rem;
.red{
  background: rgba(255, 0, 0,.5)
}
.green{
  background: rgba(0,255, 0,.5)
}
.h1{
  background: rgba(0, 0, 255,.5);
  font-size: 20/@px2rem;
}
.title-pic{
  width: 100%;
}
</style>


