import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from './components/index/index'
import axios from 'axios'


Vue.prototype.$axios = axios.create({
})
Vue.use(VueRouter);

Vue.mixin({
  created: function () {

  },
  methods: {},
  mounted: function () {}
});

const router = new VueRouter({
  routes: [{
    path: '/',
    name: 'index',
    component: Index
  }]
});

new Vue({
  data: {},
  el: '#app',
  router,
  components: {},
  created: function () {}
})



